<template>
<div class="link-telegram">
     <div class="darken">
        <div class="window link-telegram">
        <div class="highlight-block" v-if="showHighlight"></div>
        <p class="title">{{$localizeFilter('link_telegram_title')}}</p>
        <p class="text">{{$localizeFilter('link_telegram_text')}}</p>

        <span v-if="isLoaded == false">Loading...</span>
        <telegram-login-temp
            class="telegram-login-button"
            mode="callback"
            :telegram-login="botTelegramLogin"
            @loaded='telegramLoadedCallback'
            @callback="myCallback"
        />
        </div>
     </div>
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import { telegramLoginTemp } from 'vue3-telegram-login'

export default defineComponent({
    setup() {

    },
    data: ()=>({
        isLoaded: false,
        showHighlight: false
    }),
    components: {
        telegramLoginTemp
    },
    computed:{
        botTelegramLogin(){
            if(location.host == 'capitalwhale.kz'){
                return 'CapitalWhaleKzBot'
            } else if(location.host == 'capitalwhale.online') {
                return 'CapitalWhaleOnlineBot'
            } else if(location.host == 'capitalwhale.ru') {
                return 'CapitalWhaleRuBot'
            } else if(location.host == 'capitalwhale.net') {
                return 'CapitalWhaleNetBot'
            } else if(location.host == 'capitalwhale.world') {
                return 'CapitalWhaleWorldBot'
            }
            return 'CapitalWhaleBot';
        },
        host(){
            return location.host;
        }
    },
    methods: {
        telegramLoadedCallback () {
            // console.log('script is loaded')
            setTimeout(()=>{
                this.isLoaded = true
                setTimeout(()=>{
                    this.showHighlight = true
                    setTimeout(()=>{
                        this.showHighlight = false
                    }, 1500)
                }, 1000)
            }, 0)
        },
        async myCallback (user) {
            // gets user as an input
            // id, first_name, last_name, username,
            // photo_url, auth_date and hash
            // // console.log(user)

            user.clientHost = location.host;

            await this.$store.dispatch('bindTelegram', user)

            this.$emit('telegramLinked')
        }
    }
})
</script>


