<template>
    <div class="temp-exchange">
        <div class="window light" v-if="!needToLinkTelegram">
            <p class="window-title">{{$localizeFilter('temp_exchange_title')}}</p>
            <p class="window-text">{{$localizeFilter('temp_exchange_text')}}</p>
            <div class="coin-example" @click="openCoinPage(coin.name)" v-for="(coin, idx) in $store.getters.coins.filter(coin=>coin.ticker!='DOM')" v-bind:key="idx">
                <div class="coin-data-wrapper">
                    <div class="coin-image-wrapper">
                        <img class = "coin-image" :src="require(`@/assets/newImages/coins/${coin.name}.png`)">
                    </div>
                    <div class = "coin-description"><span class = "bold">{{ coin.localizedName }}</span> - {{$localizeFilter('temp_exchange_'+coin.ticker.toLowerCase())}}</div>
                </div>
                
                
                <!-- <a :href="coin.buyCoinLink || 'https://t.me/obmenRuslan'" target="_blank" v-if="coin.coinTicker!='DOM'" @click.stop> -->
                    <button class="buy-button" v-if="coin.coinTicker!='DOM'" @click.stop="buyCoin(coin.ticker)">
                        <span style="text-shadow: 4px 7px 15px rgba(0, 0, 0, 0.29);">{{$localizeFilter('buy')}}</span>
                    </button>
                <!-- </a> -->
            </div>
       </div>
       <LinkTelegramWindow v-if="needToLinkTelegram" @telegramLinked="telegramLinked"/>
        <!-- <Modal v-if="showModal && !needToLinkTelegram" :type="'welcomeBonus'" :data="welcomeBonusData" @closeModal="closeModal"/> -->
        <WelcomeBonusModal v-if="showModal && !needToLinkTelegram" :data="welcomeBonusData" @closeModal="closeModal"/>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
// import Modal from "@/components/modals/Modal.vue"
import LinkTelegramWindow from '@/components/LinkTelegramWindow.vue'
import WelcomeBonusModal from '../components/newModals/WelcomeBonusModal.vue'

export default defineComponent({
    data:  () => ({
        
    }),
    components: {
        // Modal,
        WelcomeBonusModal,
        LinkTelegramWindow
    },
    computed: {
        needToLinkTelegram(){
            return this.$store.state.info?.profile?.needToLinkTelegram || false
        }
    },
    methods: {
        openCoinPage(coinName){
            window.open(location.origin+'/#/coins/'+coinName, '_blank');
        },
        setupModal() {
            this.welcomeBonusData = JSON.parse(localStorage.exchangeWelcomeBonusData)
            if (this.welcomeBonusData != null && this.welcomeBonusData != undefined) {
                this.showModal = true
            }
        },
        async telegramLinked() {
            let profile = await this.$store.dispatch('getProfile')
            if (!profile.needToLinkTelegram) {
                this.$message(this.$localizeFilter('link_telegram_success'))
                this.setupModal()
            }
        },
        closeModal() {
            this.showModal = false
        },
        async buyCoin(coinTicker) {
            await this.$store.dispatch('setPaymentCoinTicker', {coinTicker: coinTicker})
            this.$nextTick(()=>{
                this.$store.dispatch('setShowPaymentCoinModule', {value: true})
            })
        }
    },
    async mounted() {  }
})
</script>

